:root {
  --mr-font: "Source Sans Pro", HelveticaNeue-Light, Helvetica,Arial, "Lucida Grande", sans-serif;
  --mr-body-bg: #ffffff;
  --mr-text-color: #60646c;
  --mr-heading-color: #252525;
  --mr-light-color: #999999;
  --mr-border-color: #ececec;
  --mr-border-color-hover: #d5d5d5;
  --mr-major-color: #AF3C5E;
  --mr-major-color-hover: #64284A;
  --mr-success-color: #77a464;
  --mr-info-color: #3c9acf;
  --mr-error-color: #e2401c;
}

body {
  font-family: var(--mr-font);
  background: var(--mr-body-bg);
  color: var(--mr-text-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  line-height: 1.857em;
  --tutor-primary-color: var(--mr-major-color);
  --tutor-primary-hover-color: var(--mr-major-color-hover);
  --tutor-text-color: var(--mr-text-color);
  --tutor-light-color: var(--mr-light-color);
  --tutor-success-button-color: var(--mr-success-color);
}

