  margin-bottom: 2.617924em;
  margin-left: 0;
  border-radius: 2px;
  color: #fff;
  clear: both;
  border-left: 0.6180469716em solid rgba(0, 0, 0, 0.15);
}
.woocommerce-error a:not(.button),
