input[type="submit"]:active, input[type="submit"]:hover {
  background: var(--mr-major-color-hover);
  border-color: var(--mr-major-color-hover);
  color: #fff;
}

.loading {
  color: transparent;
  opacity: .5;
  position: relative;
}
.loading::after {
  color: var(--mr-heading-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  font-weight: 400;
  line-height: 1;
  vertical-align: -.125em;
  font-family: 'Font Awesome 5 Free';
  font-weight: 900;
  line-height: inherit;
  vertical-align: baseline;
  content: "\f110";
  -webkit-animation: fa-spin .5s linear infinite;
  animation: fa-spin .5s linear infinite;
  height: 20px;
  width: 20px;
  line-height: 20px;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -10px;
  margin-top: -10px;
}

article.post-listing {
  background: #fff;
  border-radius: 4px;
  overflow: hidden;
  margin: 0;
}
article.post-listing .post-image {
  height: 192px;
  overflow: hidden;
  position: relative;
  text-indent: -99999px;
  background-size: cover;
  background-position: center center;
}
article.post-listing .post-image a {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
}

.article-search {
  border-bottom: 1px solid var(--mr-border-color);
  margin-bottom: 0;
}

.post-column {
  margin-bottom: 30px;
}

.post-content-body {
  padding: 15px 15px 10px;
  border: 1px solid var(--mr-border-color);
}
.post-content-body .entry-title {
  overflow: hidden;
  height: 2.5em;
  font-size: 20px;
  line-height: 1.25;
  margin: 5px 0 15px;
  font-weight: 600;
}
.post-content-body .entry-title a {
  color: var(--mr-heading-color);
}
.post-content-body header {
  margin-bottom: 15px;
}
.post-content-body .entry-summary {
  margin: 0 0 20px;
  font-size: 14px;
  color: var(--mr-light-color);
}
.post-content-body .entry-footer {
  border-top: 1px solid var(--mr-border-color);
  padding: 10px 15px 0;
  margin: 0 -15px;
}
.post-content-body .entry-footer .entry-meta {
  font-size: 13px;
  display: flex;
  justify-content: space-between;
  font-style: italic;
  color: var(--mr-light-color);
  flex-wrap: wrap;
}
.post-content-body .entry-footer .entry-meta a {
  font-style: normal;
  color: var(--mr-text-color);
}
.post-content-body .entry-footer .entry-meta a:hover {
  color: var(--mr-major-color);
}

.tags-links,
.cat-links {
  font-size: 14px;
}
.tags-links a,
.cat-links a {
  color: var(--mr-text-color);
  text-decoration: underline;
  font-weight: 600;
}
.tags-links a:hover,
.cat-links a:hover {
  color: var(--mr-major-color);
}

/**
 * Single Post Styles
